@import "./styles/variables/colors";

.page {
  display: grid;
  grid-template: auto / 1fr 400px;
  column-gap: 20px;
  min-height: 100vh;

  background-color: rgb(43, 40, 40);
}

.page-content {
  padding: 20px;
}

.sidebar {
  padding: 20px;
  border-left: 1px solid rgb(49, 49, 49);

  background-color: $main-color;
}
